.DontHavacnt{
    display: flex;
    margin-top: 5vh;
    justify-content: space-between;
    padding-left: 6vh;
    padding-right: 6vh;
}
.eyeIcon{
    font-size: larger;
    position: absolute;
    bottom: 0;
    right: 0;
    
}
.eyeIcon:hover{
    cursor: pointer;
}
.inpWithp{
    display: flex;
    flex-direction: column;
    width: 70%;
}
.inpWithp p{
    margin: 0;
    font-size: 18px;
    font-family: var(--fontsecondary);
    margin-bottom: 1vh;
}
.inpWithp input{
    caret-color: var(--secondary);
    font-size: 18px;
    outline: none;
    border: none;
    border-bottom: 1px solid black;
}
.loginform{
    background-color: var(--primary);
    height: 60vh;
    width: 50vh;
    border-radius: 20px;
    position: relative;
    border: 1px solid var(--secondary);
}
.mr-tp2{
    display: block;
    margin: auto;
    margin-top: 2vh;
}
.loginfieldcont{

    margin-top: 3vh;
    height: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3vh;
}

.LoginPage{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(var(--tertiary), var(--primary));
}
.passwrdcont{
    position: relative;
    width: 100% ;
}
.sec-head{
    font-family: var(--fontMain);
    width: fit-content;
    margin: auto;
    margin-top: 3vh;
}

.transparentbg{
    width: 90%;
    background-color: rgba(1,1,1,0.5);
    position: absolute;
    height: 80%;
    z-index: 0;
    filter: blur(5px);
    border-radius: 100px;
}
.textBlue{
    color: var(--tertiary);
    text-decoration: underline;
    
    
}
.textBlue:hover{
    cursor: pointer;
}